@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
html{font-family:Roboto, sans-serif;margin:0}.container{display:flex;justify-content:center}

.userInfo{color:#16151a}.userInfo__email{font-size:12px}

.todoInfo{list-style-type:none;padding:20px;border:solid 1px #16151a}.todoInfo__title{font-weight:bold;text-transform:uppercase;line-height:30px;color:#16151a}.todoInfo__status{font-size:12px;margin-bottom:20px}.todoInfo__status--completed{background-repeat:no-repeat;object-fit:cover;background-position:right;background-image:url(data:image/png;base64,)}

.list{display:grid}.list__h1{font-size:24px;color:#37363b;margin:10px auto 0}

